input {
	font-size: inherit;
}

.formCtrl {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 12%;
	width: 100%;
	font-size: inherit;
	font-weight: bold;
}

.formCtrl__small {
	width: 20%;
}

textarea {
	width: 80%;
	height: 10rem;
	max-width: 100%;
	padding: 0.5%;
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
}

.errorText {
	color: crimson;
	font-weight: bold;
}

.radio_container {
	justify-content: space-evenly;
	width: 50%;
}

.radio_dot {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-left: 90%;
}
.radio_label {
	width: 33%;
	display: flex;
	align-items: center;
	/* justify-content: space-between; */
}

.dropdownTime {
	display: flex;
	border: solid 1px red;
	background-color: chartreuse;
}

.dropdownTime_hours {
	width: 25%;
}

.hours {
	width: 75%;
}

.quarters {
	width: 75%;
}
